import { Fragment, useState } from 'react'
import { Dialog, Transition } from '@headlessui/react'
import { CheckIcon } from '@heroicons/react/24/outline'

function ButtonsChooser({ values, selected, id, setFilter }) {
  return (
    <span className="isolate inline-flex rounded-md shadow-sm">
      {values.map((v, i) => (
        <button
          type="button"
          onClick={() => setFilter(id, (v === selected ? null : v))}
          className={(i === 0 ? 'rounded-l-md' : (i === (values.length - 1) ? 'rounded-r-md' : ''))
            + " "
            + (v === selected ? "bg-indigo-600 hover:bg-indigo-500 " : "bg-white/10 hover:bg-white/20")
            + " "
            + " relative inline-flex items-center px-3 py-2 text-sm font-semibold text-white ring-1 ring-inset ring-gray-800 focus:z-10"}
        >
          {v}
        </button>

      ))}
    </span>


  )
}


function FilterForm({ filters, setFilter }) {
  return (
    <form >
      <div className="space-y-12 z-50">
        <div className="border-b border-white/10 pb-12">
          <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            {filters.map(({ id, label, values, selected }) =>
              <div className="sm:col-span-4">
                <label className="block text-sm font-medium leading-6 text-white">
                  {label}
                </label>
                <div className="mt-2">
                  <ButtonsChooser values={values} selected={selected} id={id} setFilter={setFilter} />
                </div>
              </div>
            )}

          </div>
        </div>
      </div>
    </form>

  )
}

export default function ApartmentFilters({ open, setOpen, filters, setFilter, resetFilters }) {

  return (
    <Transition.Root show={open} as={Fragment}>
      <Dialog as="div" className="relative z-10 cursor-default" onClose={setOpen} >
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
        </Transition.Child>

        <div className="fixed inset-0 z-10 overflow-y-auto">
          <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              enterTo="opacity-100 translate-y-0 sm:scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 translate-y-0 sm:scale-100"
              leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
            >
              <Dialog.Panel className="relative transform overflow-hidden rounded-lg bg-gray-800 px-4 pb-4 pt-5 text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-sm sm:p-6">
                <div>
                  <div className="mt-3 text-center sm:mt-5">
                    <Dialog.Title as="h3" className="text-base font-semibold leading-6 text-white">
                      Apartment Filters
                    </Dialog.Title>
                  </div>
                  <FilterForm filters={filters} setFilter={setFilter} />
                </div>
                <div className="mt-5 sm:mt-6 flex">
                  <button
                    type="button"
                    className="inline-flex w-full mx-2 justify-center rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                    onClick={() => setOpen(false)}
                  >
                    Apply
                  </button>
                  <button
                    type="button"
                    className="inline-flex w-full mx-2 justify-center rounded-md bg-white/10 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-white/20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-bg-white/20"
                    onClick={() => {resetFilters();setOpen(false)}}
                  >
                    Reset filters
                  </button>

                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition.Root>
  )
}
