import './App.css';
import { Model } from "./Model";
import { Suspense, useState, useRef } from 'react'
import { MapControls, Html, Stats } from '@react-three/drei';
import { Canvas, useFrame } from '@react-three/fiber';
import { useReducer } from 'react';
import { initial_state, reducer } from './reducer';
import { BuildingOffice2Icon, } from '@heroicons/react/24/outline'
import * as THREE from 'three'
import Header from "./Header"
import ApartmentsPanel from "./ApartmentsChooser"
import annotations from './annotations.json';
import { Transition } from '@headlessui/react'


function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}


function Buttons({ route, gotoAnnotation }) {
  return (
    <div id="annotationsPanel" className="md:flex md:items-center md:justify-between bg-gray-800 p-4 rounded-md">
      <div className="mt-4 flex md:mt-0">
        <nav className="flex flex-1 flex-col" aria-label="Sidebar">
          <ul className="-mx-2 space-y-1">
            {annotations.map((a) => (
              <li key={a.title}>
                <a
                  href=""
                  onClick={(e) => { e.preventDefault(); gotoAnnotation(a) }}
                  className={classNames(
                    (a.route === route)
                      ? 'bg-gray-700 text-white '
                      : 'text-gray-400 hover:text-white hover:bg-gray-700',
                    'group flex gap-x-3 rounded-md p-2 text-sm leading-6 font-semibold'
                  )}
                >
                  <BuildingOffice2Icon className="h-6 w-6 shrink-0" aria-hidden="true" />
                  {a.title}
                </a>
              </li>
            ))}
          </ul>
        </nav>
      </div>
    </div>
  )
}


function Animate({ controls, lerping, to, target }) {
  useFrame(({ camera, gl }, delta) => {

    gl.toneMapping = THREE.NoToneMapping;
    if (lerping) {
      camera.position.lerp(to, delta * 2)
      controls.current.target.lerp(target, delta * 2)
    }
    return null
  })
}

function App() {


  const [state, dispatch] = useReducer(reducer, initial_state);

  const ref = useRef()
  const [lerping, setLerping] = useState(false)
  const [to, setTo] = useState()
  const [target, setTarget] = useState()
  const routeTo = (route) => {
    dispatch({ name: "routeTo", route })
  }

  const selectApartment = (apartment) => {
    gotoAnnotation(annotations[0]);
    dispatch({ name: "selectApartment", apartment })
  }

  function gotoAnnotation({ camPos, target, lookAt, route }) {
    setTo(camPos)
    setTarget(target || lookAt)
    dispatch({ name: "routeTo", route })
    setLerping(true)
  }
  return (
    <>
      <Header routeTo={routeTo}
        gotoAnnotation={gotoAnnotation}
        route={state.route} />
      <Canvas dpr={[1, 2]}
        camera={{ position: [245.422, 22.136, 222.799] }}
        onPointerDown={() => setLerping(false)} onWheel={() => setLerping(false)}
        onCreated={({ gl }) => {
          gl.toneMapping = THREE.NoToneMapping;
          const canvas = gl.domElement;
          const width = canvas.clientWidth;
          const height = canvas.clientHeight;
          gl.setSize(width, height, false);
        }}>

        <MapControls makeDefault ref={ref} target={[119.03, -26.765, 36.794]} />

        <Suspense fallback={null}>
          <hemisphereLight intensity={2}
            color='white'
          />
          <Model waterColor={'skyblue'}
            selected={state.selectedApartment}
            hovered={state.hoveredApartment}
            route={state.route}
            routeTo={routeTo}
            onApartmentClick={selectApartment}
            onHover={(material) => dispatch({ name: "hoverMaterial", material })}
          />
          <Animate controls={ref} lerping={lerping} to={to} target={target} />
        </Suspense>
      </Canvas>
      <Transition
        show={state.route.includes("explore")}
        enter="transition-opacity duration-300"
        enterFrom="opacity-0"
        enterTo="opacity-100"
        leave="transition-opacity duration-150"
        leaveFrom="opacity-100"
        leaveTo="opacity-0"
      >
        <Buttons route={state.route} gotoAnnotation={gotoAnnotation} />
      </Transition>
      <Transition
        show={state.route.includes("apartments")}
        enter="transition-opacity duration-300"
        enterFrom="opacity-0"
        enterTo="opacity-100"
        leave="transition-opacity duration-150"
        leaveFrom="opacity-100"
        leaveTo="opacity-0"
      >
        <ApartmentsPanel title={state.apartmentsTitle}
          subtitle={state.apartmentsSubtitle}
          apartments={state.filteredApartments}
          selectedApartment={state.selectedApartment}
          onApartmentSelect={selectApartment}
          dispatch={dispatch}
          filters={state.filters} />
      </Transition>
    </>
  );
}

export default App;
