import apartments_panel from './apartments.json'

const panel = apartments_panel[0]
const apartments = panel.apartments

const filters = [
  {
    id: "bathrooms",
    label: "Bathrooms",
    values: [1, 2],
    selected: null
  },
  {
    id: "bedrooms",
    label: "Bedrooms",
    values: [1, 2, 3],
    selected: null
  },
  {
    id: "carparks",
    label: "Car parks",
    values: [0, 1, 2],
    selected: null
  }]

export const initial_state = {
  title: panel.title,
  subtitle: panel.subtitle,
  filters,
  apartments: apartments,
  filteredApartments: apartments,
  route: ["explore", "apartments"],
  selectedApartment: null,
  hoveredApartment: null
}

export function reducer(state, action) {
  console.log("### reducer", {action})
  if (action.name === "resetFilters") {
    return {...state, filteredApartments: state.apartments, filters}
  }
  if (action.name === "selectApartment") {
    return {...state, selectedApartment: action.apartment}
  }
  if (action.name === "routeTo") {
    if(state.route !== action.route)
      return {...state, route: action.route, selectedApartment: null, hoveredApartment: null}
    else
      return state;
  }
  if (action.name === "hoverMaterial") {
    return {...state, hoveredApartment: state.apartments.filter(a => a.material === action.material)[0]}
  }
  if (action.name === "hoverApartment") {
    return {...state, hoveredApartment: action.apartment}
  }
  if (action.name === "setFilter") {
    let { id, value } = action;
    let filters = state.filters.map(filter => filter.id === id ? { ...filter, selected: value } : filter)

    let activeFilters = filters.filter(({selected}) => selected)

    let filteredApartments = activeFilters
        ? state.apartments.filter(a => activeFilters.every(({id,selected}) => a[id] === selected))
        : state.apartments

    return {...state, filters, filteredApartments}
  }
  return state
}
