import { ChevronRightIcon, FunnelIcon, XMarkIcon } from '@heroicons/react/20/solid'
import ApartmentsFilters from "./ApartmentsFilters"
import { Fragment, useState } from 'react'


function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}

const statuses = {
  offline: 'text-gray-500 bg-gray-100/10',
  available: 'text-green-400 bg-green-400/10',
  error: 'text-rose-400 bg-rose-400/10',
}

export default function ApartmentsPanel({title,subtitle,filters,apartments,selectedApartment,onApartmentSelect,dispatch}) {
  const [open, setOpen] = useState(false)
  return (<div id="apartmentsPanel" className="md:flex md:items-center md:justify-between bg-gray-800 p-4 rounded-md cursor-default">
    <div className="mt-4 flex md:mt-0">
      <nav className="flex flex-1 flex-col" aria-label="Sidebar">
        <h3 className="text-base font-semibold leading-7 text-white flex">
          {title}
          <span className="flex-1" />
          <FunnelIcon className="h-5 w-5 flex-none text-gray-400 hover:text-gray-200 cursor-pointer"
            onClick={() => setOpen(true)} />
          <XMarkIcon className="h-5 w-5 flex-none text-gray-400 hover:text-gray-200 cursor-pointer"
                      onClick={() => dispatch({name: "resetFilters"})} />
        </h3>
        <div className="text-xs font-semibold leading-6 text-gray-400">{subtitle}</div>
        <ApartmentsFilters open={open} setOpen={setOpen} filters={filters}
                           resetFilters={() => dispatch({name:"resetFilters"})}
                           setFilter={(id, value) =>{
          dispatch({ id, value, name: "setFilter" })}} />
        <ul className="-mx-2 mt-2 space-y-1">
          <li>
            <ul className="divide-y divide-white/5">
              {apartments.map((apartment) => (
                <li key={apartment.id}
                    className={classNames(apartment === selectedApartment ? "bg-indigo-600 hover:bg-indigo-500" : "hover:bg-gray-400/20","cursor-pointer  relative flex items-center space-x-4 px-4 py-4 sm:px-6 lg:px-8")}
                    onMouseOver={() => dispatch({name:"hoverApartment", apartment})}
                    onMouseLeave={() => dispatch({name:"hoverApartment", apartment: null})}
                    onClick={() => {onApartmentSelect(apartment); }}

                >
                  <div className="min-w-0 flex-auto">
                    <div className="flex items-center gap-x-3">
                      <div className={classNames(statuses[apartment.status], 'flex-none rounded-full p-1')}>
                        <div className="h-2 w-2 rounded-full bg-current" />
                      </div>
                      <h2 className="min-w-0 text-sm font-semibold leading-6 text-white">
                        <a href={apartment.href} className="flex gap-x-2">
                          <span className="truncate">{apartment.name}</span>
                          <span className="text-gray-400">/</span>
                          <span className="whitespace-nowrap">{apartment.floor}</span>
                          <span className="absolute inset-0" />
                        </a>
                      </h2>
                    </div>
                    <div className="mt-3 flex items-center gap-x-2.5 text-xs leading-5 text-gray-400">
                      <p className="truncate">{apartment.price}</p>
                      <svg viewBox="0 0 2 2" className="h-0.5 w-0.5 flex-none fill-gray-300">
                        <circle cx={1} cy={1} r={1} />
                      </svg>
                      <p className="whitespace-nowrap">{apartment.description}</p>
                    </div>
                  </div>
                  <div
                    className={classNames(
                      'text-gray-400 bg-gray-400/10 ring-gray-400/20',
                      'rounded-full flex-none py-1 px-2 text-xs font-medium ring-1 ring-inset'
                    )} >
                    {/* {apartment.floorplan} */}
                    Floorplan
                  </div>
                  <ChevronRightIcon className="h-5 w-5 flex-none text-gray-400" aria-hidden="true" />
                </li>
              ))}
            </ul>
          </li>
        </ul>
      </nav>
    </div>
  </div>)
}
